.App {
  min-height: 100vh;
  background-image: linear-gradient(to top, #a8edea 0%, #fed6e3 100%);
}
.search_form {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.search_input {
  width: 50%;
  border: none;
  padding: 10px;
}
input:focus {
  outline: none;
}
.search_button {
  background: lightcoral;
  border: none;
  padding: 10px 20px;
  color: whitesmoke;
  outline: none;
}
.recipes {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
